import React, { useState } from "react";
import {
  Formik,
  Field,
  Form,
  ErrorMessage,
  useFormikContext,
  useField,
} from "formik";
import * as Yup from "yup";
import { Alert, FormGroup } from "reactstrap";
import DatePicker from "react-datepicker";
import moment from "moment";

import { createSchedule } from "../../../api/scheduleCallAPI";

import "react-datepicker/dist/react-datepicker.css";
import { useTranslation } from "react-i18next";

const DatePickerField = ({ ...props }) => {
  const { t } = useTranslation();
  const { setFieldValue } = useFormikContext();
  const [field] = useField(props);
  return (
    <DatePicker
      {...field}
      {...props}
      selected={(field.value && new Date(field.value)) || null}
      onChange={(val) => {
        setFieldValue(field.name, val);
      }}
      minDate={new Date()}
      placeholderText={t("scheduleCall.txt_38")}
      showTimeSelect
      dateFormat="yyyy-MM-dd h:mm aa"
      withPortal
      showYearDropdown
      scrollableYearDropdown
      timeIntervals={15}
    />
  );
};

const phoneRegExp =
  /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

const ScheduleCallForm = () => {
  const { t } = useTranslation();
  const [isFailSubmission, setIsFailSubmission] = useState(false);
  const [errorMsg, setErrorMsg] = useState({ head: "", sub: "" });
  const [isLoading, setIsLoading] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);

  function successSubmission() {
    setIsSuccess(true);
  }

  function failSubmission(head, sub) {
    setIsFailSubmission(true);
    setIsLoading(false);
    setErrorMsg({
      head: head,
      sub: sub,
    });
  }

  return (
    <div className="container ">
      {!isSuccess && (
        <>
          <div className="row d-flex justify-content-center">
            <div className="col-sm-8 center ">
              <h3 className="sch-form-head-text ">
                {t("scheduleCall.txt_15")}
              </h3>
            </div>
          </div>
          <div className="row d-flex justify-content-center">
            <div className="col-sm-12">
              <div className="invoice-form-div">
                <Formik
                  initialValues={{
                    name: "",
                    phoneNumber: "",
                    email: "",
                    whenNeed: "",
                    loanAmount: "",
                    scheduledDate: "",
                  }}
                  validationSchema={Yup.object({
                    name: Yup.string()
                      .min(5, t("scheduleCall.txt_32"))
                      .max(100, t("scheduleCall.txt_33"))
                      .required(t("scheduleCall.txt_34")),
                    email: Yup.string()
                      .max(100, t("scheduleCall.txt_35"))
                      .email(t("scheduleCall.txt_36"))
                      .required(t("scheduleCall.txt_34")),
                    phoneNumber: Yup.string().matches(
                      phoneRegExp,
                      t("scheduleCall.txt_37")
                    ),
                    whenNeed: Yup.string().required(t("scheduleCall.txt_34")),
                    loanAmount: Yup.string().required(t("scheduleCall.txt_34")),
                    scheduledDate: Yup.string().required(
                      t("scheduleCall.txt_34")
                    ),
                  })}
                  onSubmit={(values, { setSubmitting }) => {
                    setIsLoading(true);
                    setIsFailSubmission(false);

                    createSchedule({
                      fullName: values.name,
                      email: values.email,
                      phoneNumber: values.phoneNumber,
                      whenNeed: values.whenNeed,
                      amount: values.loanAmount,
                      scheduleDate: moment(values.scheduledDate).format(
                        "YYYY-MM-DD HH:mm:ss"
                      ),
                    }).then((data) => {
                      try {
                        if (data["result"]) {
                          if (data["result"] === "SUCCESS") {
                            successSubmission(data["data"]);
                          } else if (data["result"] === "FAILED") {
                            failSubmission(
                              "Error!",
                              data["data"]["errorMessage"]
                            );
                          } else {
                            failSubmission("Error!", "Please try again later!");
                          }
                        } else {
                          failSubmission("Error!", "Please try again later!");
                        }
                      } catch (error) {
                        failSubmission("Error!", "Please try again later!");
                      }
                    });
                  }}
                >
                  <Form>
                    <div className="row d-flex justify-content-center">
                      <div className="col-sm-10 mt-4 mb-4">
                        <div className="row">
                          <div className="col-sm-6 mb-3">
                            <p className="sch-form-label">
                              {t("scheduleCall.txt_16")}
                            </p>
                            <FormGroup className="">
                              <Field
                                name="name"
                                type="text"
                                className="form-control"
                                placeholder={t("scheduleCall.txt_16")}
                                disabled={isLoading}
                              />
                              <ErrorMessage name="name">
                                {(msg) => (
                                  <div style={{ color: "red" }}>{msg}</div>
                                )}
                              </ErrorMessage>
                            </FormGroup>
                          </div>
                          <div className="col-sm-6 mb-3">
                            <p className="sch-form-label">
                              {t("scheduleCall.txt_17")}
                            </p>
                            <FormGroup className="input-box">
                              <Field
                                name="phoneNumber"
                                type="number"
                                className="form-control"
                                placeholder={t("scheduleCall.txt_17")}
                                disabled={isLoading}
                              />
                              <ErrorMessage name="phoneNumber">
                                {(msg) => (
                                  <div style={{ color: "red" }}>{msg}</div>
                                )}
                              </ErrorMessage>
                            </FormGroup>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-sm-12 mb-3">
                            <p className="sch-form-label">
                              {t("scheduleCall.txt_18")}
                            </p>
                            <FormGroup className="">
                              <Field
                                name="email"
                                type="text"
                                className="form-control"
                                placeholder={t("scheduleCall.txt_18")}
                                disabled={isLoading}
                              />
                              <ErrorMessage name="email">
                                {(msg) => (
                                  <div style={{ color: "red" }}>{msg}</div>
                                )}
                              </ErrorMessage>
                            </FormGroup>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-sm-6 mb-3">
                            <p className="sch-form-label">
                              {t("scheduleCall.txt_19")}
                            </p>
                            <FormGroup className="">
                              <Field
                                name="whenNeed"
                                as="select"
                                className="form-control"
                                placeholder=""
                                disabled={isLoading}
                              >
                                <option value="Im already in contract">
                                  {t("scheduleCall.txt_23")}
                                </option>
                                <option value="1 to 2 months">
                                  {t("scheduleCall.txt_24")}
                                </option>
                                <option value="From 3 to 6 months">
                                  {t("scheduleCall.txt_25")}
                                </option>
                                <option value="More than 6 months">
                                  {t("scheduleCall.txt_26")}
                                </option>
                              </Field>
                              <ErrorMessage name="whenNeed">
                                {(msg) => (
                                  <div style={{ color: "red" }}>{msg}</div>
                                )}
                              </ErrorMessage>
                            </FormGroup>
                          </div>
                          <div className="col-sm-6 mb-3">
                            <p className="sch-form-label">
                              {t("scheduleCall.txt_20")}
                            </p>
                            <FormGroup className="">
                              <Field
                                name="loanAmount"
                                as="select"
                                className="form-control"
                                placeholder=""
                                disabled={isLoading}
                              >
                                <option value="Less than $150,000">
                                  {t("scheduleCall.txt_27")}
                                </option>
                                <option value="Between $150,001 - $200,000">
                                  {t("scheduleCall.txt_28")}
                                </option>
                                <option value="Between $200,001 - $250,000">
                                  {t("scheduleCall.txt_29")}
                                </option>
                                <option value="Between $250,001 - $350,000">
                                  {t("scheduleCall.txt_30")}
                                </option>
                                <option value="More than $350,000">
                                  {t("scheduleCall.txt_31")}
                                </option>
                              </Field>
                              <ErrorMessage name="loanAmount">
                                {(msg) => (
                                  <div style={{ color: "red" }}>{msg}</div>
                                )}
                              </ErrorMessage>
                            </FormGroup>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-sm-6 mb-3">
                            <p className="sch-form-label">
                              {t("scheduleCall.txt_21")}
                            </p>
                            <FormGroup className="">
                              <DatePickerField
                                className="form-control"
                                name="scheduledDate"
                              />
                              <ErrorMessage name="scheduledDate">
                                {(msg) => (
                                  <div style={{ color: "red" }}>{msg}</div>
                                )}
                              </ErrorMessage>
                            </FormGroup>
                          </div>
                        </div>
                        {isFailSubmission && (
                          <div className="container">
                            <div className="row">
                              <div className="col text-center">
                                <>
                                  <Alert color="danger">
                                    {/* <p>{errorMsg.head}</p>
                                <hr /> */}
                                    <p className="mb-0">{errorMsg.sub}</p>
                                  </Alert>
                                </>
                              </div>
                            </div>
                          </div>
                        )}

                        <div className="container">
                          <div className="row">
                            <div className="col-sm-12 text-center">
                              <button
                                type="submit"
                                className="schedule-call-submit btn"
                                disabled={isLoading}
                              >
                                {t("scheduleCall.txt_22")}
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </Form>
                </Formik>
              </div>
            </div>
          </div>
        </>
      )}
      {isSuccess && (
        <div className="container">
          <div className="row">
            <div className="col text-center">
              <>
                <Alert color="success">
                  {/* <p>{errorMsg.head}</p>
                                <hr /> */}
                  <p className="mb-0">{t("scheduleCall.txt_39")}</p>
                  <a href="/">{t("scheduleCall.txt_40")}</a>
                </Alert>
              </>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ScheduleCallForm;
